
import {computed, defineComponent, onMounted} from 'vue'
import { useRouter } from 'vue-router';
export default defineComponent({
    components:{

    },
    props:{
        //Name  ID 
        data:{
            type:Object,
            default:{}
        },
        category:{
            type:String,
            default:{},
        },
    },
    setup({category,data}){
        const router = useRouter()
        const dataKeyList = computed(()=>{ //dataScenehotelID key
            return data[Object.keys(data)[0]]
        })
        switch(category){
            case "" :
                break
        }
        function drag (e:any) {
            e.dataTransfer.setData('text/plain',dataKeyList.value ) //id
        }
        function gotItemPage (data:any) {
            let temp = JSON.stringify(data)
            router.push({
                path:`/ItemDisplay`,
                query:{
                    data:temp
                }
            })
        }
        return{
            //methods
            drag,gotItemPage,dataKeyList
        }
    }
})
